<template>
  <div id="app"  style="width:1200px;margin:auto">
    <el-container style="background: #94a59b;">
      <el-header style="min-height:76px; z-index:10000;">
        <div id="menu-bar" style="position: fixed; min-height:76px; width: 100%">
          <el-col :span="1" >
            <a @click="switchLang">中/En</a>
          </el-col>
          <el-col :span="6">
            <div style="font-size: 16px;width: 100%; text-align:center;background: #589016">CCDC 2019
            June 3-7</div>
            </el-col>
          <el-col :span="17" >
            <nav-menu/>
          </el-col>
        </div>
      </el-header>
      <el-main>
        <router-view/>
      </el-main>
      <el-footer  height="200px">
        <div style="text-align: left; line-height: 28px; padding: 20px;">
          中国东北大学 <br/>
          中国自动化学会信息物理系统控制与决策专业委员会<br/>
          IEEE 控制系统协会<br/>
          中国人工智能学会(CAAI)<br/>
          中国自动化控制理论专业委员会<br/>
          东北大学 流程工业综合自动化国家重点实验室<br/>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import NavMenu from '@/components/Nav'
export default {
  name: 'App',
  components: {
    NavMenu
  },
  methods: {
    switchLang () {
      let locale = this.$i18n.locale
      locale === 'zh' ? this.$i18n.locale = 'en' : this.$i18n.locale = 'zh'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



