<template>
  <div>
    <div style="height: 300px;background-color: #0c41a3">
      <el-button @click="toggleLang('zh')">{{ $t("message.hello") }}</el-button>
      <el-button @click="changeLocale()">中文/English</el-button>
      <el-button @click="getInfo()">axios</el-button>
      <img src=""/>
    </div>
    <el-col :span="16">
      <article>
        <p>
         {{$t("content.home_introduce")}}
        </p>
        <h2 >{{$t("message.hello")}}11111</h2>   <!--v-html相当于innerHTML-->
        <h3 v-text="message"></h3>   <!--v-text相当于innerText-->
        <h3 v-html="$t('message.hello')"></h3>   <!--v-text相当于innerText-->
{{info}}
      </article>
    </el-col>
    <el-col :span="8">
      <div style="padding: 15px;margin-top: 15px">
        <li>征文通知截稿日期变更</li>
      </div>
    </el-col>

  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      info: null
    }
  },
  methods: {
  // js方法
    changeLocale () {
      this.$confirm(this.$t('layer.toggle'), this.$t('layer.tips'), {
        confirmButtonText: this.$t('button.ok'),
        cancelButtonText: this.$t('button.cancel'),
        type: 'warning'
      }).then(() => {
        let locale = this.$i18n.locale
        locale === 'zh' ? this.$i18n.locale = 'en' : this.$i18n.locale = 'zh'
      }).catch(() => {
        this.$message({
          type: 'info'
        })
      })
    },
    toggleLang (lang) {
      if (lang === 'zh-CN') {
        localStorage.setItem('locale', 'zh-CN')
        this.$i18n.locale = localStorage.getItem('locale')
        this.$message({
          message: '切换为中文！',
          type: 'success'
        })
      } else if (lang === 'en') {
        localStorage.setItem('locale', 'en')
        this.$i18n.locale = localStorage.getItem('locale')
        this.$message({
          message: 'Switch to English!',
          type: 'success'
        })
      }
    },
    changeLanguage () {
      let lang = this.$i18n.locale === 'zh' ? 'zh' : 'en'
      this.$i18n.locale = lang
    },
    getInfo () {
      alert('Ceshi')
      axios.get('https://api.coindesk.com/v1/bpi/currentprice.json')
        .then(response => (this.info = response))
    }
  }
}
</script>

<style>
</style>



