var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('h1',{staticStyle:{"color":"red"}},[_vm._v("重要通知")]),_vm._v(" "),_c('h2',[_vm._v("重要通知")]),_vm._v(" "),_c('h3',[_vm._v("重要通知")]),_vm._v(" "),_c('h4',[_vm._v("hello")]),_vm._v(" "),_c('h5',[_vm._v("zhongyao")]),_vm._v(" "),_c('h6',[_vm._v("zuixiaobiaoti")]),_vm._v(" "),_c('abbr',[_vm._v("23432432423")]),_vm._v(" "),_c('em',[_vm._v("123")]),_vm._v(" "),_c('acronym',[_vm._v("i am Ok.")]),_vm._v(" "),_c('hr'),_vm._v(" "),_c('article',[_vm._v("第30届中国控制与决策会议(2018CCDC)将于2018年6月9日-11日在中国沈阳举行。会议由东北大学和中国自动化学会信息物理系统控制与决策专业委员会主办。沈阳是辽宁省省会，国家历史文化名城，素有“一朝发祥地，两代帝王都”之称。是中国最重要的以装备制造业为主的重工业基地，有着“共和国长子”和“东方鲁尔”的美誉。先后获得“国家环境保护模范城市”、“国家森林城市”、“国家园林城市”称号。")]),_vm._v(" "),_c('p',[_vm._v("\n  中国控制与决策会议(CCDC)概况 中国控制与决策会议(CCDC)，是由《控制与决策》编辑委员会联合中国自动化学会应用专业委员会、中国航空学会自动控制专业委员会等学术组织，于1989年创办的全国性大型学术会议。此会每年举行一次，至今已经连续举办了29届。最近几年，每年参会人数都在近千人左右，2017年参会人数超过了1200人，其中有国内著名的专家，也有国外和海外的知名学者。大家齐聚一堂，交流学术思想，讨论学术问题，充满了浓厚的学术气氛。 从1991年起，会议论文集由《控制与决策》编辑部编辑，东北大学出版社出版。最近几年，每年论文集收入的文章都在1000篇以上。为了加入国际检索系统，所有论文都加了英文文题、作者、单位、摘要和关键词。许多论文是由国家和省部级基金资助的论文。 最近几年，会议邀请国外和海外的知名专家学者作大会学术报告。其中2005年大会报告人有：俄罗斯科学院院士瓦西列夫教授，日本东京大学古田 胜久教授，香港城市大学陈关荣教授；2006年大会报告人有：日本东京大学堀洋一教授，新加坡南洋理工大学王郸维博士。2008年大会报告人有: Xi-Ren Cao、Tianyou Chai、Graham C. Goodwin、Thom J. Hodgson和Miroslav Krstic。另外，自2008年起增加了准大会报告，详情请参看“History”。\n")]),_vm._v(" "),_c('p',[_vm._v("\n  第30届中国控制与决策会议将更加国际化。本次会议由东北大学和中国自动化学会信息物理系统控制与决策专业委员会主办。2018CCDC会议论文集英文论文将进人IEEE Xplore Data Base，并将被EI检索。 主办单位： 东北大学 东北大学始建于1923年4月26日，是一所具有爱国主义光荣传统的大学。1928年8月至1937年1月，著名爱国将领张学良将军兼任校长。1949年3月，在东北大学工学院和理学院（部分）的基础上成立沈阳工学院。1950年8月，定名为东北工学院，1993年3月，复名为东北大学，1997年1月原沈阳黄金学院并入东北大学，1998年9月划转为教育部直属高校，并与辽宁省共建的全国重点大学。在90余年的办学历程中，东北大学始终坚持与国家发展和民族复兴同向同行,形成了“自强不息、知行合一”校训精神。历史上，东北大学师生曾是“一二?九”运动的主力和先锋，在建设时期，学校先后研发出国内第一台模拟电子计算机、第一台国产CT、第一块超级钢以及钒钛磁铁矿冶炼新技术、钢铁工业节能理论和技术、控轧控冷技术、混合智能优化控制技术等一大批高水平科研成果，兴办了第一个大学科学园，培育了东软、东网等高新技术企业，在技术创新、转移和产学研合作方面形成了自己的办学特色. 学校现有教职工4516人，其中专任教师2673人。其中，中国科学院和中国工程院院士5人，外籍院士2人，国家“千人计划”入选者20人，青年千人计划13人。国家“高层次人才特殊支持计划”入选者6人，教育部“长江学者奖励计划”特聘教授、讲座教授25人，国家杰出青年基金获得者23人，海外及港澳学者合作研究基金获得者14人，教育部新世纪优秀人才102人，国家\"百千万人才工程\"入选者13人。国家自然科学基金创新群体4个，教育部创新团队3个。博士生导师495人；学校设有100多个研究机构，其中国家重点实验室3个，国家工程（技术）研究中心4个，国家工程实验室3个。设有国家级协同创新中心2个，辽宁省协同创新中心3个。 学校学科涵盖哲学、经济学、法学、教育学、文学、理学、工学、管理学、艺术学等门类。设有66个本科专业，其中国家级特色专业15个；有177个学科有权招收和培养硕士研究生（另设10个专业学位授权点），108个学科有权招收和培养博士研究生；有17个博士后流动站；3个一级学科国家重点学科，4个二级学科国家重点学科，1个国家重点（培育）学科，共涵盖16个二级学科。学校以一流为目标、以改革为动力推进学科布局优化和调整，高峰引领、高原支撑、卓越促进、特色牵动，可持续发展的学科建设格局正在形成。 东北大学具备培养学士、硕士、博士和博士后的完整教育体系。学校全日制在校生40000余人，其中本科生29804人，硕士研究生10362人，博士研究生3730人。学校围绕立德树人的根本任务，在拔尖创新型人才培养、教学理念更新、教学方法研究、培养模式探索等方面取得了丰硕成果。“十二五”以来，东北大学共获得国家级教学成果奖3项，国家级精品视频公开课11门，国家级精品资源共享课课程15门，国家级“十二五”规划教材18种。获得全国优秀博士学位论文提名5篇。学生获得创新创业竞赛国际大奖400项、国家级奖励1687项、优秀创新创业项目1580项，专利近384项，共有50家学生创业企业落地。学生生源质量、毕业生就业率及就业质量保持较高水平。 学校坚持服务国家战略和区域发展的科研方向，鼓励自由探索，推动协同创新，基础研究水平稳步提高，技术创新的竞争力不断增强。“十二五”以来，学校承担各类科技项目6387项，获各类科技奖励279项，其中国家级奖励14项,省部级一等奖58项；获得国家专利1499项，其中发明专利1127项；被三大检索收录的论文共14644篇。主（承）办国际、国内学术会议161次，其中国际会议65次。 学校在技术创新和成果转化方面形成了独特的比较优势，探索出了一条政产学研用相结合的有效途径，实现了学科、人才、科研、产业良性互动发展。2016年，学校高新技术产业销售收入136亿元，科技产业综合指标名列全国高校第三位。学校坚持面向新一代信息技术、新材料、生命健康等领域，新建云计算科技园、新材料产业园、健康产业园，推进云计算和大数据产业快速发展，推动辽沈地区装备制造业创新升级，为东北大学服务国民经济建设和东北老工业基地全面振兴奠定了新的重要基础。 学校面向世界，开放办学，先后与35个国家和地区的212所大学、研究机构建立了长期稳定的合作关系，每年聘请300多位海外知名专家来校讲学或合作科研；派出教职工出国（境）开展学术交流近700人次；派出600多名各层次学生赴海外高校交流学习。2016年有来自98个国家和地区的各类留学生1408人在校学习。 面向未来，东北大学将继续遵循“教育英才”的办学宗旨，全面提升人才培养、科学研究、社会服务和文化传承创新的大学功能，坚定地走“创新型、特色化、开放式”发展道路，为建成“在中国新型工业化进程中起引领作用的‘中国特色、世界一流’大学”而奋斗。 中国自动化学会信息物理系统控制与决策专业委员会 中国自动化学会信息物理系统控制与决策专业委员是中国自动化学会下设的二级分支机构。专委会于2016年4月成立，旨在为信息物理系统控制与决策领域的专业人士提供学术交流平台，注重培养本领域的青年人才，为推动国内相关学术研究和产业起到积极作用。自2017年以来，参与主办中国控制与决策会议。\n")]),_vm._v(" "),_c('li',[_vm._v("  第30届中国控制与决策会议将更加国际化。")]),_vm._v(" "),_c('footer',[_vm._v("hello, zhongguokongzhiyujueces")])],1)}
