import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/views/Home'
import Guide from '@/views/ConferenceGuide'
import CallForPapers from '@/views/authors/CallForPapers'
import Committees from '@/views/conference/Committees'
import ZhangSiYing from '@/views/conference/zhangsiying'
import History from '@/views/conference/history'
import Hotel from '@/views/location/hotel'
import city from '@/views/location/City'
import keynote from '@/views/program/keynote'
import distinguished from '@/views/program/distinguishedLectures'
import firstsubmit from '@/views/authors/firstsubmit'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/guide',
      name: 'Guide',
      component: Guide
    },
    {
      path: '/callforpapers',
      name: 'CallForPapers',
      component: CallForPapers
    },
    {
      path: '/committees',
      name: 'Committees',
      component: Committees
    },
    {
      path: '/zhang',
      name: 'ZhangSiYing',
      component: ZhangSiYing
    },
    {
      path: '/history',
      name: 'History',
      component: History
    },
    {
      path: '/keynote',
      name: 'keynote',
      component: keynote
    },
    {
      path: '/distinguished',
      name: 'distinguished',
      component: distinguished
    },
    {
      path: '/firstsubmit',
      name: 'firstsubmit',
      component: firstsubmit
    },
    {
      path: '/hotel',
      name: 'Hotel',
      component: Hotel
    },
    {
      path: '/city',
      name: 'city',
      component: city
    }
  ]
})



