var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('article',[_c('p',[_vm._v("关于《张嗣瀛（CCDC）优秀青年论文奖》的实施方案")]),_vm._v(" "),_c('p',[_vm._v(" 张嗣瀛，中国科学院院士，是中国控制界人所敬仰的资深专家，是《控制与决策》杂志和《中国控制与决策会议（CCDC）》的创始人。张先生在学术研究与教书育人方面的卓越成就与贡献赢得了同行的高度评价和钦佩，并获得到多项国家和省部级奖励。为了弘扬张嗣瀛院士高尚的科学精神、学术品格与为人情操，也为了鞭策和激励年轻一代学者以张先生为楷模，潜心做学问，坦荡谋人生，本会决定设立《张嗣瀛（CCDC）优秀青年论文奖》。并制定如下实施方案。\n1．  奖项名称")]),_vm._v(" "),_c('p',[_vm._v(" ☆ 张嗣瀛（CCDC）优秀青年论文奖；\n        ☆ 《CCDC优秀青年论文奖》每年1-2篇；\n        ☆ 《CCDC优秀青年论文奖提名奖》，每年3-4篇。")]),_vm._v("\n2．  评奖委员会\n        ☆ 聘请国内外知名控制与决策领域专家组成，负责组织论文的评审与颁奖。\n        ☆ 评奖委员会主任：1人，\n        ☆ 评奖委员会副主任：1人，\n        ☆ 评奖委员会委员：3-5人。\n3．  获奖条件\n        ☆ 论文第一作者年龄不超过35周岁；\n        ☆ 所有作者都不是评奖委员会委员；\n        ☆ 论文第一作者亲自在会上宣读，并接受提问；\n        ☆ 论文工作成果具有很好的理论创新性或重要的应用价值，并从未正式发表；\n        ☆ 对于能够将先进控制理论与实际结合并取得良好应用成果的给予重点考虑；\n        ☆ 论文结构严谨，内容阐述清晰，文献引用恰当，结论明确可信；\n        ☆ 论文用英文撰写并用英文报告。\n4．  评奖过程\n        ☆ 投稿时需要注明“申请评奖”，并附第一作者的工作证（或学生证、身份证、护照）复印件；\n        ☆ 论文要通过会议的一般性审稿，审稿人推荐参加评奖；\n        ☆ 由评奖委员会邀请有关专家作书面评审；\n        ☆ 当年CCDC终审会上确定入选论文；\n        ☆ 入选论文应由论文第一作者在中国控制与决策会议上宣读，并接受提问；\n        ☆ 由评奖委员会根据论文质量及宣读水平，评出获奖论文和提名奖论文；\n        ☆ 在会议颁奖仪式上宣布评选结果并授奖。\n        ☆ 当参加本次会议的评奖委员不足全体委员的三分之二时，评奖委员会主任可以聘请参加会议的知名专家增补为本次会议评奖委员。\n5．  奖金\n        ☆ 张嗣瀛（CCDC）优秀青年论文奖奖金10000元/篇，并颁发获奖证书；\n        ☆ 张嗣瀛（CCDC）优秀青年论文奖提名奖奖金3000元/篇，并颁发获奖证书。\n6．  其它\n        ☆ 本实施方案由CCDC主办单位和CCDC组委会制定。\n      "),_c('p',[_vm._v("\nInformation for 29th CCDC “Zhang Si-Ying Outstanding Youth Paper Award”\nThe Award Committee for the 2017 CCDC was composed of five members: Professor Zhong-Ping Jiang of USA, Professor Karl H. Johansson of Sweden, Professor Changyun Wen of Singapore, Professor Xinhe Xu of China and Professor Guang-Hong Yang of China.\n      ")]),_vm._v("\nFor the 2017 CCDC, 127 papers were selected for consideration for the “Zhang Si-Ying Outstanding Youth Paper Award” based on reviewers' comments, nominations, and the evaluations of the Technical Program Committee members. These papers were sent to famous experts including some members of the International Advisory Committee for further evaluation. Based on their evaluations and recommendation, the Technical Program Committee shortlisted the following five papers as the finalists for the Award:\n\nWinners for Zhang Si-Ying Outstanding Youth Paper Award\n        Xinyue Shen and Yuantao Gu of Tsinghua University, Steven Diamond and Stephen Boyd of Stanford University, Madeleine Udell of Cornell University.\n        Paper Title: Disciplined Multi-Convex Programming\n        Yong Qiao and Wenlun Yang of Zhejiang University, Minyue Fu of Zhejiang University and University of Newcastle\n        Paper Title: A New Distributed Method for Clock Synchronization in Sensor Networks\nFinalists for Zhang Si-Ying Outstanding Youth Paper Award\n         Xin Du and Peter Benner of Max Planck Institute for Dynamics of Complex Technical Systems and Shanghai University.\n           Paper Title:Parameterized Frequency-dependent Balanced Truncation for Model Order Reduction of Linear Systems\n        Menglin Li and Jiahu Qin of University of Science and Technology of China, Ling Shi of Hong Kong University of Science and Technology, Xinghuo Yu of RMIT University and Huijun Gao of Harbin Institute of Technology.\n           Paper Title:Optimal DoS Attack Strategy against Remote State Estimation Over Lossy Networks\n        He Cai and Guoqiang Hu of Nanyang Technological University\n           Paper Title:Cooperative Output Regulation for a Class of Nonlinear Heterogeneous Multi-Agent Systems\n\n    ")])])}]
